import { Button, TableBody, TableCell, TableHead, TableRow } from '@material-ui/core';
import React, { useState, useEffect } from 'react';
import { createAPIEndpoint, ENDPOINTS } from '../../api';
import Table from '../../layouts/Table';
import DeleteOutlineIcon from '@material-ui/icons/DeleteOutline';
import DehazeIcon from '@material-ui/icons/Dehaze';

const OrderList = (props) => {

    const { setOrderId, setOrderListVisibility, resetFormControls, setNotify } = props;

    const [orderList, setOrderList] = useState([]);

    useEffect(() => {
        createAPIEndpoint(ENDPOINTS.ORDER).fetchAll()
        .then(res => {
            setOrderList(res.data);
        })
        .catch(err => console.log(err));
    }, []);

    const showForUpdate = id => {
        setOrderId(id);
        setOrderListVisibility(false);
    }

    const deleteOrder = id => {
        if(window.confirm('Are you sure to delete this record ?')){
            createAPIEndpoint(ENDPOINTS.ORDER).delete(id)
            .then(res => {
                setOrderListVisibility(false);
                setOrderId(0);
                resetFormControls();
                setNotify({ isOpen: true, message: 'Delete order successfully.' });
            })
            .catch(err => console.log(err));
        }
    }

    return (
        <Table>
            <TableHead>
                <TableRow>
                    <TableCell>Order Number</TableCell>
                    <TableCell>Customer</TableCell>
                    <TableCell>Pay Method</TableCell>
                    <TableCell>Grand Total</TableCell>
                    <TableCell></TableCell>
                </TableRow>
            </TableHead>
            <TableBody>
                {
                    orderList.map(item => (
                        <TableRow key={item.orderId}>
                            <TableCell>
                                {item.orderNumber}
                            </TableCell>
                            <TableCell>
                                {item.customer.customerName}
                            </TableCell>
                            <TableCell>
                                {item.pMethod}
                            </TableCell>
                            <TableCell>
                                {item.gTotal}
                            </TableCell>
                            <TableCell>
                                <Button onClick={e => showForUpdate(item.orderId)}>
                                    <DehazeIcon 
                                    color="primary" />
                                </Button>
                                <Button onClick={e => deleteOrder(item.orderId)}> 
                                    <DeleteOutlineIcon 
                                    color="secondary" />
                                </Button>
                            </TableCell>
                        </TableRow>
                    ))
                }
            </TableBody>
        </Table>
    );
};

export default OrderList;