import React from 'react';
import { Button, ButtonGroup, IconButton, List, ListItem, ListItemSecondaryAction, ListItemText, Paper, makeStyles } from '@material-ui/core';
import DeleteOutlineIcon from '@material-ui/icons/DeleteOutline';
import { roundTo2DecimalPoint } from '../../utils';

const useStyles = makeStyles(them =>({
    paperRoot: {
        margin: '15px 0px',
        '&:hover': {
            cursor: 'pointer'
        },
        '&:hover $deleteButton':{
            display: 'block'
        }
    },
    buttonGroup: {
        backgroundColor: '#e3e3e3',
        borderRadius: 8,
        '& .MuiButtonBase-root':{
            border: 'none',
            minWidth: '25px',
            padding: '1px'
        },
        '& button:nth-child(2)': {
            fontSize: '1.2rem',
            color: '#000'
        }
    },
    deleteButton: {
        display: 'none',
        '& .MuiButtonBase-root':{
            color: '#e81719'
        },
    },
    totalPerItem: {
        fontWeight: 'bolder',
        fontSize: '1.2em',
        margin: '0px 10px'
    }
}));

const OrderedFoodItem = (props) => {

    const { values, setValues } = props;
    const classes = useStyles();

    let orderedFoodItems = values.orderDetails;

    const removeFoodItem = (index, id) => {
        let x = {...values};
        x.orderDetails = x.orderDetails.filter((_, i) => i != index);
        if(id != 0){
            x.deletedOrderItemIds += id + ',';
        }
        setValues({...x});
    }

    const updateQuantity = (idx, value) => {
        let x = { ...values };
        let foodItem = x.orderDetails[idx];
        if(foodItem.quantityItem + value > 0){
            foodItem.quantityItem += value; 
            setValues({...x});
        }
    }   

    return (
        <List>
            {
                orderedFoodItems.length == 0 ? 
                <ListItem>
                    <ListItemText
                    primary = "Please select food items" 
                    primaryTypographyProps={{
                        style: {
                            textAlign: 'center',
                            fontStyle: 'italic'
                        }
                    }}/>
                </ListItem>
                : orderedFoodItems.map((item, idx) => (
                    <Paper key={idx} className={classes.paperRoot}>
                        <ListItem>
                            <ListItemText
                                primary={item.foodItemName}
                                primaryTypographyProps={{
                                    component: 'h1',
                                    style: {
                                        fontWeight: '500',
                                        fontSize: '1.2rem'
                                    }
                                }}
                                secondary={
                                    <>
                                    <ButtonGroup size="small" className={classes.buttonGroup}>
                                        <Button
                                        onClick={e => updateQuantity(idx, -1)}>-</Button>
                                        <Button disabled>{item.quantityItem}</Button>
                                        <Button
                                        onClick={e => updateQuantity(idx, 1)}>+</Button>
                                    </ButtonGroup>
                                    <span className={classes.totalPerItem}>
                                        { roundTo2DecimalPoint(item.foodItemPrice * item.quantityItem) +' VND' }
                                    </span>
                                    </>
                                }
                                secondaryTypographyProps={{
                                    component: 'div'
                                }}
                            />
                            <ListItemSecondaryAction className={classes.deleteButton}>
                                <IconButton 
                                onClick={ e => removeFoodItem(idx, item.orderDetailId) }
                                disableRipple>
                                    <DeleteOutlineIcon/>
                                </IconButton>
                            </ListItemSecondaryAction>
                        </ListItem>
                    </Paper>
                ))
            }
        </List>
    );
};

export default OrderedFoodItem;